        og: `During 1858, as the construction works commenced in the land plot that Tsar Alexander II recently purchased just outside the city walls next to Jaffa Gate (known as Moscow Square), a fascinating discovery of an enormous stone pillar is made.
        
        The length of the pillar is 12 meters, its diameter 1.75 meters and its weight approximately 60 tons. The big column is made of the so-called Jewish stone (Hebrew: mizit yehudit, Arabic: mizzi yahudi), a specific variety of the world-renowned Jerusalem stone; a dark grey or yellow crystalline dolomite or dolomitic limestone, appreciated for its hardness which made it an excellent building material for the city of Jerusalem and its surroundings. Varieties of the Jerusalem stone have been used in numerous famous constructions in the area of Jerusalem, such as the Western Wall.

        Because of its size, the column is attributed to the Herodian period, which constitutes the epax of building activity in the land of Israel and Yehuda. Archaeologists estimate that the unearthed pillar was intended for the construction of the Second Temple relying on Yosef ben Matityahus (Flavius Josephus) testimony; he mentions huge columns 27-feet high (aprox. 8.25 meters) being used for the construction of the Second Temple on Temple Mount. Other experts claim it to be impossible that this column was used for the construction of the Second Temple, as it is larger than 27 feet along with the fact that in the Herodian period it was customary to build using columns created of multiple sections of stone in contrast to this monolithic one.

        Since its discovery and because of its size, the pillar has been known as the Finger of Og among Jerusalems residents in the name of Og, king of Bashan, who is described in the Bible as a person of great size. According to a Talmudic text he was the only person to survive the Great Flood apart from Noah.
        `
